<template>
  <div
      class="text_fit"
      :class="{visible: ready}"
      ref="text_fit"
      :style="textFitStyles"
  >
    <span ref="text">
      <slot/>
    </span>
  </div>
</template>

<script>
export default {
  name: 'TextFit',
  data() {
    return {
      ready: false,
      fontSize: '1rem',
      lineHeight: 1,
    }
  },
  computed: {
    textFitStyles() {
      return {
        'font-size': this.fontSize,
        'line-height': this.lineHeight,
        'height': this.lineHeight,
      }
    }
  },
  methods: {
    fitText() {
      this.ready = false

      let textFitParentHeight =  this.$refs.text_fit.parentElement.clientHeight

      this.fontSize = textFitParentHeight * 0.6 + 'px'
      this.lineHeight = textFitParentHeight * 0.9 + 'px'

      this.$nextTick(() => {
        const textFitWidth =  this.$refs.text_fit.clientWidth
        const textWidth = this.$refs.text.getBoundingClientRect().width

        if (textWidth > textFitWidth) {
          textFitParentHeight *= textFitWidth / textWidth
          this.fontSize = textFitParentHeight * 0.6 + 'px'
        }

        this.ready = true
      })
    }
  },
}
</script>

<style scoped lang="scss">
.text_fit {
  width: 100%;
  height: 100%;
  opacity: 0;
  text-align: center;

  &.visible {
    opacity: 1;
  }
}
</style>
