<template>
  <div class="timer_object">
    <div class="round">
      <TextFit ref="text_fit">{{ slide.time }}</TextFit>
    </div>
  </div>
</template>

<script>
import TextFit from "@/components/utils/FitText";

export default {
  name: 'TimerObject',
  components: {TextFit},
  props: ['value'],
  data() {
    return {
      timeLeft: 0,
    }
  },
  computed: {
    slide() {
      return this.$store.state.slides.find(slide => slide.id === this.value)
    },
  },
  watch: {
    'slide.time'() {
      this.onResize()
    }
  },
  methods: {
    onResize() {
      this.$nextTick(this.$refs.text_fit.fitText)
    },
  },
  created() {
    // this.timeLeft = this.$store.getters.activeSlide.time
    // setInterval(() => this.timeLeft--, 1000)
  }
}
</script>

<style scoped lang="scss">
